
import { defineComponent } from 'vue'
import MultiColumnView from "@/components/containers/MultiColumnView.vue"
import { Option } from '../Forms/FieldInterface'
import ViewPort from "@/components/DataViews/ViewPort.vue"
import SelectMixin from "@/components/FormElements/SelectMixin.vue"
import {
    IonItem,
    IonLabel,
} from "@ionic/vue"
import { find } from "lodash"

export default defineComponent({
    name: "HisSelectGrid",
    mixins: [SelectMixin],
    components: {
        IonItem,
        IonLabel,
        ViewPort,
        MultiColumnView
    },
    data: () => ({
        columnsPerRow: 4,
        isInit: false as boolean
    }),
    mounted() {
        this.init()
    },
    activated() {
        this.init()    
    },
    methods: {
        clearValue() { 
            this.clearSelection() 
        },
        async init() {
            this.$emit('onFieldActivated', this)
            this.listData = await this.options(this.fdata)
            if (!this.isInit) await this.setDefaultValue()
            if (typeof this.config.columnsPerRow === 'number') {
                this.columnsPerRow = this.config.columnsPerRow
            }
            this.isInit = true
        },
        async setDefaultValue() {
            if(this.defaultValue) {
                const defaults: string = await this.defaultValue(this.fdata, this.cdata, this.selected)
                if (defaults) {
                    const found = find(this.listData, {label: defaults}) || find(this.listData, {value: defaults}) 
                    if (found) {
                        this.onselect(found)
                    } else {
                        this.selected = defaults
                    }
                }
            }
        },
        async onselect(item: Option | null) {
            this.selected = item ? item.label : ''
            if (this.onValue) {
                this.selected = (await this.onValue(item, this)) || ''
                if (!this.selected) return
            }
            this.$emit('onValue', item)
        }
    }
})
